<template>
    <div class="margin">
        <div v-if="account.address">
            <span>
                地址:
                <a @click="login">{{ address }}</a>
                余额:{{ balanceOne | twoDecimals }} ONE
            </span>
            <span  @click.prevent="clickRecord" class="record">
            <RadioGroup v-model="record" type="button" size="small">
                <Radio label="true">我的记录</Radio>
            </RadioGroup>
            </span>
        </div>
        <div v-else>
            <Button type="text" size="small" @click="login" :loading="loging">登录</Button>
        </div>
    </div>
</template>

<script>
import { twoDecimals } from "../js/num";
export default {
    name: "Account",
    data: function() {
        return {
            record: "",
            loging:false,
        };
    },
    model: {
        prop: "checked",
        event: "change"
    },
    props: ["checked"],
    methods: {
        clickRecord(){
            this.record = this.record=="true"?"":"true";
            this.$emit('change', this.record!="true");
        },
        message(type, content) {
            this.$Message[type]({
                background: true,
                content,
                duration: 5
            });
        },
        login: async function() {
            this.loging=true;
            try {
                await this.$store.dispatch("logout");
                await this.$store.dispatch("login");
            } catch (err) {
                this.message("error", err.message);
            }
            this.loging=false;
        }
    },
    filters: { twoDecimals } ,
    computed: {
        address() {
            if (this.account.address == undefined) return "";
            return (
                this.account.address.slice(0, 6) +
                "..." +
                this.account.address.slice(-4, -1)
            );
        },
        balanceOne() {
            return this.account.balance.toOne();
        },
        account() {
            return this.$store.state.account;
        }
    }
};
</script>

